/*pseudo styles for "placeholder"*/
::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  /*font-style: italic;*/
  letter-spacing: 0.1rem;
  opacity: 0.5;
}
::-moz-placeholder { /* Firefox 19+ */
  color: pink;
}
:-ms-input-placeholder { /* IE 10+ */
  color: pink;
}
:-moz-placeholder { /* Firefox 18- */
  color: pink;
}

input[type=text] {
