}

.key-results-container {
  position: relative;
}
.key-results-container-toggle {
  position: absolute;
  top: -33px;
  right: 0;
  background-color: #f1f1f1;
  color: #2b2b2b;
  font-weight: bold;
  border: 1px solid #fff;
  border-radius: 5px;
  padding: 5px 10px;
}

.key-results-container-toggle:active {
  box-shadow: inset 0 0 5px 1px #fff;
}

.key-results {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  /*height: 100vh;*/
  -webkit-align-content: flex-start;
          align-content: flex-start;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  /*align-items: space-around;*/
  
  padding: 10px 10px 0 0;
  border: 1px solid #2b2b2b;
  border-radius: 5px;
  overflow-y: scroll;
  max-height: 200px;
}

.key-results.hide {
  display: none;
}

.key-results li {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
  /*flex-basis: calc(auto, );*/
  margin: 0 0 10px 10px;
  text-align: center;
  /*padding: 0.5em 1em;*/
  border: 1px solid #000;
  border-radius: 5px;
  /*display: flex;*/
  /*justify-content: space-around;*/
}

.key-results li button {
  display: block;
  background-color: #f9f9f9!important;
  background-size: 25px!important;
  color: #2b2b2b;
  /*text-align: right;*/
  /*padding: 0.5em 1em;*/
  padding: 10px 5px 10px 30px;
  border-radius: 5px;
  width: 100%;
  height: 100%;
  font-size: 1rem;
  letter-spacing: 0.1rem;
}

.key-results li button:active {
  box-shadow: inset 0 0 5px 1px #2b2b2b;
}

.key-results li button span {
  font-size: 0.9em;
  font-weight: bold;
  padding-left: 5px;
}

