/* Media Queries */


@media only screen
  and (min-device-width: 768px) {
    .container {
      padding: 0 15px;
    }
    .App-header {
      /*padding: 0.5rem 0 1rem;*/
      /*padding: 0 0 15px;*/
    }
    .main-content {
      padding-top: 50px;
      -webkit-flex-direction: row;
              flex-direction: row;
      -webkit-justify-content: space-between;
              justify-content: space-between;
    }
    .main-content h2 { 
      margin-bottom: 1rem;
    }
    .main-content > div {
      /*flex: 0 1 370px;*/
      -webkit-flex: 1 1;
              flex: 1 1;
      /*flex-basis: 45%;*/

    }
    .main-content > div.main {
      -webkit-flex-basis: 40%;
              flex-basis: 40%
      /*flex-basis: 50%*/
    }
    .main-content > div.hunter-status {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-basis: 60%;
              flex-basis: 60%;
      /*flex-basis: 50%;*/
      /*margin-left: 15px;*/
    }
    .main-content > div.hunter-status > div {
      -webkit-flex: 1 1;
              flex: 1 1;
    }

    .main-content > div.hunter-status .skills-list, .main-content > div.hunter-status .status-list {
      margin-left: 15px;
    }

    .hunter-ui-card {
      border-radius: 5px;
